    /*position: relative;*/
    width: 100%;
    overflow: hidden;
}

.wtc-choose-calendar .wtc-choose-calendar-week {
    display: flex;
    font-size: 12px;
    /*border-top: 1px solid #e5e5e5;*/
    padding: 10px 0;
    color: #212121;
    background: #F5F5F5;
}

.wtc-choose-calendar .weekend {
  color: #FF991C;
}

.wtc-choose-calendar-week .wtc-choose-calendar-week-item {
    flex: 1;
    text-align: center;
}

.wtc-choose-calendar .wtc-choose-calendar-head {
    text-align: center;
    padding: 10px 0;
}

.wtc-choose-calendar .wtc-choose-calendar-head .wtc-choose-calendar-cur-month {
    font-weight: 500;
    font-size: 14px;
    color: #212121;
}

.wtc-choose-calendar-head > div {
    display: inline-block;
    padding: 0 10px;
    font-size: 20px;
    vertical-align: bottom;
}

.wtc-choose-calendar-head .wtc-choose-calendar-left-month-btn-disable {
    color: #ccc;
}

.wtc-choose-calendar .wtc-choose-calendar-content-wrap {
    width: 300%;
    display: flex;
}
.wtc-choose-calendar .wtc-choose-calendar-content {
    font-size: 0;
    width: 100%;
}

.wtc-choose-calendar-content .wtc-choose-calendar-content-date-wrap {
    position: relative;
    font-size: 14px;
    display: inline-block;
    width: 14.2%;
    height: 14.2vw;
    /*height: 54px;
    padding: 5px 0px;*/
    margin: 4px 0px;
    box-sizing: border-box;
    cursor: pointer;
    padding: 5px;
}

.wtc-choose-calendar-content .pre-date,
.wtc-choose-calendar-content .next-date {
    /*opacity: 0.5;*/
  opacity: 0;
    color: #fff;
}

.wtc-choose-calendar-content .choosed-date .wtc-choose-calendar-content-date {
    /*border: 1px solid #276ff5;
    background-color: #f2f9ff;
    color: #276ff5;*/
  border: 0px solid #276ff5 !important;
  border-radius: 50% !important;
  background-color: #276ff5 !important;
  color: #ffffff !important;
}

.wtc-choose-calendar-content .middle-start-choosed-date {

  border-top-left-radius: 50%;
  border-bottom-left-radius: 50%;
}

.wtc-choose-calendar-content .middle-end-choosed-date {
  border-top-right-radius: 50%;
  border-bottom-right-radius: 50%;
}

.wtc-choose-calendar-content .middle-choosed-date  {
  background-color: #E0EFFF;
}

.wtc-choose-calendar-content .choosed-date .wtc-choose-calendar-content-date .wtc-choose-calendar-content-date-apply .wtc-choose-calendar-content-date-apply-inner {
  background-color: #fffff1 !important;
}

