.wtc-choose-calendar-content .wtc-choose-calendar-content-date {
  position: relative;
  height: 100%;
  box-sizing: border-box;
  overflow: hidden;
}
.wtc-choose-calendar-content .wtc-cld-date-today {
  color: #276ff5;
}
.wtc-choose-calendar-content .wtc-cld-date-actual-off {
  color: #999;
}
.wtc-choose-calendar-content .wtc-cld-date-rest {
  color: #999;
}
.wtc-choose-calendar-content .wtc-cld-date-actual-work {
  color: #212121;
}
.wtc-choose-calendar-content .wtc-cld-date-actual-off .wtc-choose-calendar-content-date-mark {
  background-color: #ff854d;
}
.wtc-choose-calendar-content .wtc-cld-date-actual-work .wtc-choose-calendar-content-date-mark {
  background-color: #16b8b1;
}
.wtc-choose-calendar-content-date .wtc-choose-calendar-content-date-mark {
  position: absolute;
  right: 0;
  top: 0;
  /* height: 18px; */
  font-size: 12px;
  width: 16px;
  height: 16px;
  color: #fff;
  border-radius: 2px;
  text-align: center;
}
.wtc-choose-calendar-content-date .wtc-choose-calendar-content-date-text {
  line-height: 100%;
  text-align: center;
  font-size: 16px;
  font-weight: 400;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}
.wtc-choose-calendar-content-date .wtc-choose-calendar-content-date-apply {
  position: absolute;
  top: 80%;
  left: 50%;
  transform: translateX(-50%);
}
.wtc-choose-calendar-content-date .wtc-choose-calendar-content-date-apply .wtc-choose-calendar-content-date-apply-inner {
  width: 4px;
  height: 4px;
  border-radius: 50%;
  background-color: #276ff5;
}
